<template>	
	<el-row type="flex" justify="start">
		<el-input placeholder="搜你想搜" v-model="searchValue" class="search-input">
			<el-button icon="el-icon-search" circle slot="append" @click.native="search" :loading="searching"><span class="search-logo">搜索</span></el-button>
		</el-input>
		<el-card :body-style="{ padding: '10px' }"  class="module" v-for="item in items" :key="item.id">
			<el-col class="module-concent">
				<router-link :to="'/'+ item.moduleName + '/' + item.id">
					<span class="module-title">{{ item.title || ''}}</span>
				</router-link>
				<div class="module-description">
					<span>{{ item.description || ''}}</span>
				</div>
				<div class="module-info">
					<span>创建时间: {{ item.createTime }}</span>
				</div>
			</el-col>
		</el-card>
	</el-row>
</template>

<script>
	export default {
		name: 'index',
		data(){
			return {
				items: [],
				searchValue: '',
				searching: false,
				module: {},
			}
		},
		methods: {
			search(){
				(async () => {
					try{
						this.searching = true
						let res = await this.$api.web.getItems({
							keyWords : this.searchValue
						})
						if(res.status === 2000){
							this.items = res.items
							this.searching = false
						}else{
							this.$message.error('服务器繁忙')
						}
					}catch(e){
						this.$message.error('服务器繁忙')
						console.log(e)
					}
				})()
			}
		},
		created: function(){
			(async () => {
				try{
					let res = await this.$api.admin.getReview()
					if(res.status === 2000){
						this.items = res.items;
					}else{
						this.$message.error('服务器繁忙');
					}
				}catch(e){
					this.$message.error('服务器繁忙');
					console.log(e);
				}
				

			})()
		}
	}
</script>

<style scoped lang="scss">
	@import '@/assets/public/css/cross-browser.scss';
	@import '@/assets/public/css/box.scss';
	@import '@/assets/public/css/font.scss';
	.el-row{
		flex-flow: column wrap;
	}
	.search-input{
		margin: 10px 10px 10px 0px;
		max-width: 90%;
	}
	.search-logo{
		color: #409eff;
		&:hover{
			color: #66b1ff;
		}
	}
	.module{
		@include index-main-box;
		margin-bottom: 20px;
		.module-title{
			@include title-font;
		}
		.module-description{
			@include description-font;
			padding-right: 40px;
		}
		.module-info{
			@include info-font;
			text-align: right;
			div{
				margin-bottom: 10px;
			}
		}
		.module-concent{
			text-align: left;
			> * {
				padding: 10px;
				height: 100%;	
			}
		}
		.module-time{
			@include info-font;
		}
	}
</style>


